import { useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import { InputBox, PasswordBox, ButtonBox, CheckBox, Buttons, SelectBox } from "../../../components/RenderFroms";
import Breadcrumbs from "../../../components/Breadcrumbs";
import ListTitle from "../../../components/ListTitle";

const defaultValue = {
	login_name: "",
	name: "",
	menu_style: "",
	select_record: "",
	login_password: "",
	confirm_password: ""
}

export default function EditProfile() {

	const validationSchemaInfo = Yup.object().shape({
		name: Yup.string().required("Name is required"),
		menu_style: Yup.string().required("This is required"),
		select_record: Yup.string().required("This is required"),
		confirm_password: Yup.string().oneOf([Yup.ref('login_password')], 'Confirm Password must match'),
		login_name: Yup.string()
			.email("Invalid email address")
			.required("Email is Required"),

	});

	const handleUpdate = (values: any) => {
		console.log("values", values)
	}

	const menuOptions = [{ label: 'Collapsed', value: 'collapsed' }, { label: 'Expanded', value: 'expanded' }]
	const recordOptions = [{ label: '25', value: '25' }, { label: '50', value: '50' }, { label: '100', value: '100' }, { label: 'All', value: 'all' }]

	return (
		<>
			<Breadcrumbs root="" pageName="Profile" />

			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">
							<ListTitle ListName="Edit Your Profile (* indicates mandatory field(s))" />

							<div className="form-horizontal has-validation-callback">
								<Formik
									initialValues={defaultValue}
									validationSchema={validationSchemaInfo}
									onSubmit={(values: any) => handleUpdate(values)}
								>
									{({ handleChange, handleBlur, handleSubmit, values, errors }) => (
										<>
											<div className="card-body">

												<InputBox
													required={true}
													name="login_name"
													lable="Login Username"
													placeholder=""
												/>

												<InputBox
													required={true}
													name="name"
													lable="Your Name"
													placeholder=""
												/>

												<SelectBox
													required={true}
													options={menuOptions}
													name="menu_style"
													lable="Menu Style"
													placeholder="Select"
												/>


												<div className="form-group" style={{ marginTop: '30px' }}>
													<label htmlFor="MenuStyle" className="col-lg-3 col-sm-2 control-label"></label>
													<div className="col-md-9 mb-5">
														<strong className="red">NOTE: Supply Passwords below only if you want to change.</strong>
													</div>
												</div>

												<PasswordBox

													name="login_password"
													lable="Login Password"
													placeholder=""
												/>

												<PasswordBox
													name="confirm_password"
													lable="Confirm Login Password"
													placeholder=""
												/>
												<div className="clear"></div>
											</div>


											<div className="card-action">
												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
														<Buttons loading={false} className="btn-success" value="Save Profile" onClick={() => handleSubmit()} />
													</div>
												</div>
												<div className="clear"></div>
											</div>

										</>)}
								</Formik>
							</div>
						</div>
					</div>
				</div>
			</div>


		</>
	);
}


